import { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from 'react-router-dom';
import { Breadcrumb } from "app/components";
import * as Yup from 'yup';
import axios from 'axios';
import {
  Card, Alert,
  Divider,
  CardContent,
  CardHeader,
  Box,
  Grid,
  Button,
  styled,
  useTheme,
  Table, TableHead, TableRow, TableCell, TableBody
} from "@mui/material";
import Moment from 'moment';

const Item = styled("div")(({ theme }) => (
  {
    marginBottom: "5px",
    fontSize: '16px',
    fontWeight: '400',
    color: '#484747'
  }
));
const ItemContent = styled("div")(({ theme }) => (
  {
    marginBottom: "15px",
    fontSize: '16px',
    fontWeight: '600',
  }
));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

// form field validation schema
const validationSchema = Yup.object().shape({
  status: Yup.string()
    .required('User Accton is required!'),
});

const Small = styled('small')(({ bgcolor }) => ({
  width: 50,
  height: 15,
  color: '#fff',
  padding: '5px 8px',
  borderRadius: '4px',
  overflow: 'hidden',
  background: bgcolor,
  boxShadow: '0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24)',
  cursor: 'pointer'
}));

const ViewPurchaseDetails = () => {
  const { palette } = useTheme();
  const bgError = palette.error.main;
  const bgPrimary = palette.primary.main;
  const bgSecondary = palette.secondary.main;
  const bgSuccess = palette.success.main;
  const bgSuccessLight = palette.success.light;
  const bgSecondaryDark = palette.secondary.dark;

  const { order_id } = useParams();
  const theme = useTheme();
  const navigate = useNavigate();
  const [order, setOrder] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/get-purchase-order-details', {
          row_id: order_id
        })
          .then(response => {
            if (response.data.response_code === 200) {
              setOrder(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Purchase Details" }]} />
        </Box>

        <Card title="Purchase Details">
          <ContentBox>
            <CardHeader
              title="Purchase Details"
              titleTypographyProps={{
                variant: "h4",
                fontSize: '18px',
                fontWeight: '600',
                // textTransform: 'uppercase',
                color: theme.palette.primary.main,

              }}
              action={
                <>
                  <Button title="Back" component={Link}
                    to={`/dashboard/order-history/purchase/list`}
                    color="inherit" variant="outlined" size="small" sx={{ m: .5 }}>
                    Back
                  </Button>
                </>
              }
            />
            <Divider />
            <CardContent>
              <Box width="100%" overflow="auto">
                {order && (
                  <>
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Order Id</Item>
                        <ItemContent>{order.order_id}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Transaction Id</Item>
                        <ItemContent>{order.transaction_id}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Payment Status</Item>
                        <ItemContent>
                          {(() => {
                            switch (order.payment_status) {
                              case 'success':
                                return <Small bgcolor={bgSuccess}>Success</Small>
                              case 'pending':
                                return <Small bgcolor={bgSecondary}>Pending</Small>
                              case 'failed':
                                return <Small bgcolor={bgError}>Failed</Small>
                              default:
                                return null
                            }
                          })()}
                        </ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Total MRP</Item>
                        <ItemContent>{process.env.REACT_APP_CURRENCY} {order.total_mrp}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Additional Discount</Item>
                        <ItemContent>{process.env.REACT_APP_CURRENCY} {order.additional_discount}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Delivery Charges</Item>
                        <ItemContent>{process.env.REACT_APP_CURRENCY} {order.delivery_charges}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Total Saving</Item>
                        <ItemContent>{process.env.REACT_APP_CURRENCY} {order.total_savings}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Total Payable</Item>
                        <ItemContent><Small bgcolor={bgSuccess}>{process.env.REACT_APP_CURRENCY} {order.total_payable}</Small></ItemContent>
                      </Grid>

                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Order Date</Item>
                        <ItemContent>{Moment(order.created_at).format('DD/MM/YYYY')}</ItemContent>
                      </Grid>

                    </Grid>

                    <br />
                    <br />
                    <Divider />

                    <br />
                    <br />
                    <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }}>Order Summary</h4>
                    <Grid container>
                      <Grid item sm={12} xs={12} lg={12}>
                        <Box overflow="auto">
                          <Table>
                            <TableHead>
                              <TableRow>
                                <TableCell align="center">Product </TableCell>
                                <TableCell align="center">Manufacturar</TableCell>
                                <TableCell align="center">Seller</TableCell>
                                <TableCell align="center">Unit Packing</TableCell>
                                <TableCell align="center">Quantity</TableCell>
                                <TableCell align="center">Rate</TableCell>
                                <TableCell align="center">Total MRP</TableCell>
                                <TableCell align="center">Total</TableCell>
                                <TableCell align="center">Status</TableCell>
                              </TableRow>
                            </TableHead>
                            <TableBody>
                              {order.order_items && (
                                order.order_items.map((row, index) => (
                                  <>
                                    <TableRow key={`order-summary-${index}`}>
                                      <TableCell align="center">
                                        <a className="hover_effect1" title={row.product_name} href={`/product/details/${row.id}`}>
                                          <img className='os-list-img'
                                            src={row.product_image}
                                            alt={row.product_name}
                                            style={{ width: "120px" }}
                                          />
                                        </a>
                                        <br />
                                        <strong>{row.product_name}</strong>
                                      </TableCell>
                                      <TableCell align="center">{row.brand_name}</TableCell>
                                      <TableCell align="center">{row.seller_name}</TableCell>
                                      <TableCell align="center">{row.unit_packing}</TableCell>
                                      <TableCell align="center">{row.quantity}</TableCell>
                                      <TableCell align="center">{row.min_qty_net_rate} / {row.min_qty} items</TableCell>
                                      <TableCell align="center">{process.env.REACT_APP_CURRENCY} {row.total_mrp}</TableCell>
                                      <TableCell align="center">{process.env.REACT_APP_CURRENCY} {row.total_payable}</TableCell>
                                      <TableCell align="center">
                                        {(() => {
                                          switch (row.status) {
                                            case 'pending':
                                              return <Small bgcolor={bgSecondary}>Pending</Small>
                                            case 'failed':
                                              return <Small bgcolor={bgError}>Failed</Small>
                                            case 'cancelled':
                                              return <Small bgcolor={bgError}>Cancelled</Small>
                                            case 'completed':
                                              return <Small bgcolor={bgPrimary}>Completed</Small>
                                            case 'processing':
                                              return <Small bgcolor={bgSuccessLight}>Processing</Small>
                                            case 'ready_to_ship':
                                              return <Small bgcolor={bgSuccessLight}>Ready To Ship</Small>
                                            case 'shipped':
                                              return <Small bgcolor={bgSuccessLight}>Shipped</Small>
                                            case 'delivered':
                                              return <Small bgcolor={bgSuccess}>Delivered</Small>
                                            case 'return':
                                              return <Small bgcolor={bgSecondaryDark}>Return</Small>
                                            case 'return_initiated':
                                              return <Small bgcolor={bgSecondaryDark}>Return Initiated</Small>
                                            case 'refund':
                                              return <Small bgcolor={bgError}>Refunded</Small>
                                            default:
                                              return null
                                          }
                                        })()}
                                      </TableCell>
                                    </TableRow >

                                  </>
                                ))
                              )}
                            </TableBody>
                          </Table>
                          <div className='order_summary os-left' style={{ width: "350px", float: "right", padding: "20px" }}>
                            <Table sx={{ border: "0px" }} aria-label="simple table" key={'price-detail'}>
                              <TableBody sx={{ '&:last-child td, &:last-child th': { border: 0 } }} key={'price-detail-bd'}>
                                <TableRow>
                                  <TableCell align="left"><ItemContent>Total MRP</ItemContent></TableCell>
                                  <TableCell align="right"><ItemContent>{process.env.REACT_APP_CURRENCY}. {order.total_mrp}</ItemContent></TableCell>
                                </TableRow>
                                <TableRow>
                                  <TableCell align="left"><ItemContent>Additional Discount</ItemContent></TableCell>
                                  <TableCell align="right"><ItemContent>{process.env.REACT_APP_CURRENCY}. {(order.additional_discount) ?? 0.0}</ItemContent></TableCell>
                                </TableRow>

                                <TableRow className='row-highlighter'>
                                  <TableCell align="left"><ItemContent>Total Savings</ItemContent></TableCell>
                                  <TableCell align="right"><ItemContent>{process.env.REACT_APP_CURRENCY}. {(order.total_savings) ?? 0.0}</ItemContent></TableCell>
                                </TableRow>
                                <TableRow>
                                  <TableCell align="center" colSpan={2} sx={{ wordBreak: "unset" }}>
                                    <Alert severity="info" icon={false}>
                                      *Delivery charges are applicable when the product is delivered based on the actual weight of the consignment.
                                    </Alert>
                                  </TableCell>
                                </TableRow>
                                <TableRow>
                                  <TableCell align="left" colSpan={2}><Divider /></TableCell>
                                </TableRow>

                                <TableRow className='total-payable'>
                                  <TableCell align="left"><ItemContent>Total Payable</ItemContent></TableCell>
                                  <TableCell align="right">
                                    <ItemContent><span className='price'>
                                      {process.env.REACT_APP_CURRENCY}. {order.total_payable}
                                    </span>
                                    </ItemContent>
                                  </TableCell>
                                </TableRow>
                              </TableBody>
                            </Table>
                          </div>
                        </Box>
                      </Grid>
                    </Grid>
                  </>
                )}
              </Box>
            </CardContent>
          </ContentBox>
        </Card>

      </Container >
    </>

  );
};

export default ViewPurchaseDetails;
